import React from 'react'
import './card.css';
// import Hero from '../../images/hero1.png';

const Card = ({ hero }) => {
  return (
    <div className="card">
      <div className="container">    
       {/* 
          How to get a photo in Public folder
          => Using absolute path
          => https://think0wise.tistory.com/21
       */}
        <img src={`${process.env.PUBLIC_URL}/img/hero${hero.id}.png`} alt="hero" className="imageItem" />
        {/* <img src={Hero} alt="hero" className="imageItem" /> */}
        <h3>{hero.name}</h3>
        <p>{hero.email}</p>
      </div> 
    </div>      
  )
}

export default Card
