import './searchBox.css';

const SearchBox = ({ setSearchItems }) => {

  const searchInput = (e) => {
    setSearchItems(e.target.value);
  }

  return (
    <div className="search">
      <div className="container">
        <input 
          type="search" 
          placeholder="Search Hero"           
          onChange={searchInput}  
        />
      </div>
    </div>
  )
}

export default SearchBox
