import { useState, useEffect } from 'react';
import './App.css';
import SearchBox from './components/searchBox/SearchBox';
import CardList from './pages/cardList/CardList';

function App() {
  const [searchItems, setSearchItems] = useState("")
  const [heroes, setHeroes] = useState([])

  // Getting all heroes information
  useEffect(() => {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(response => response.json())
      .then(users => setHeroes(users))      
  }, [])

  // Getting heroes filtered with search keyword
  const filteredHeroes = heroes.filter(hero => 
    hero.name.toLowerCase().includes(searchItems.toLowerCase())
  );

  return (
    <div className="app">
      <h1>Game Heroes</h1>
      <SearchBox setSearchItems={setSearchItems} />
      <CardList heroes={filteredHeroes} />
    </div>
  );
}

export default App;
